<template>
  <scroll class="listview" 
          :data = "data" 
          ref="listview"
          :listenScroll="listenScroll"
          @scroll="scroll"
          :probeType="probeType"
          >
    <ul>
      <li v-for="group in data" class="list-group" ref="listGroup">
        <h2 class="list-group-title">{{group.title}}</h2>
        <ul>
          <li 
          v-for="item in group.items" 
          class="list-group-item"
          @click="selectItem(item)"
          >
            <img class="avatar" v-lazy="item.avatar"/>
            <span class="name">{{item.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
    <div class="list-shortcut" @touchstart = "onShortcutTouchStart" @touchmove = "onShortcutTouchMove">
      <ul>
        <li v-for="item,index in shortcutList" 
            :data-index="index" 
            class="item" 
            ref="ll"
            :class="{'current':currentIndex == index}"
            >
          {{item}}
        </li>
      </ul>
    </div>
    <div class="list-fixed" v-show="fixTitlt" ref = 'fixed'>
      <h1 class="fixed-title">{{fixTitlt}}</h1>
    </div>
    <div v-show="data.length">
      <loading></loading>
    </div>
  </scroll>
</template>

<script type="text/ecmascript-6">
import Scroll from 'base/scroll/scroll' 
import {getData} from 'common/js/dom'
import loading from 'base/loading/loading'
const titleHeight = 30 
export default {
  props:{
    data:{
      type:Array,
      default:[]
    }
  },
  data(){
    return {
      scrollY:-1,
      currentIndex:0,
      def: -1
    }
  },
  created(){
      this.touchStart = 0/*滑动开始的值*/
      this.touchMove = 0/*滑动中的值*/
      this.touchNub = 0/**/
      this.listenScroll = true
      this.listHeigth = []
      this.probeType = 3
  },
  computed: {
    shortcutList(){/*对data数据进行重行划定输出的是一个新的数组是我们右侧快速入口的数组*/
      return this.data.map((group)=>{
        return group.title.substr(0,1)
      })
    },
    fixTitlt(){
      if(this.scrollY > 0){
        return ''
      }
      return this.data[this.currentIndex] ? this.data[this.currentIndex].title:''
    }
  },
  components:{
    Scroll,
    loading
  },
  methods:{
    refresh(){
      this.$refs.listview.refresh()
    },
    selectItem(item){//当我点击的时候 将点击的当前项的信息传递给调用的父组件
      this.$emit('select',item)
    },
    onShortcutTouchStart(e){
        /*getData输出的是当*/
      let anchorIndex = getData(e.target,'index')/*获取当前的快速入口的被点击的元素的data-index属性
        该值和我们生成的scroll中的listGroup的index值又是一一对应的
      */
     
      this.scerollTo(anchorIndex)
      this.touchStart = e.touches[0].pageY
    },
    onShortcutTouchMove(e){//移动时获取当前的y轴位置
      this.touchMove = e.touches[0].pageY
      this.touchNub = Math.ceil((this.touchMove-this.touchStart)/18)/*这样与开始时获取到的y'轴距离相减在初一
        除以每个字的高度 就可以得到移到第几个字母上
      */
      this.scerollTo(this.touchNub)
    },
    scerollTo(index){
      if(!index && index !==0){//滚动时的边界判断
        return
      }
      if(index < 0){//当idnex 小于0 即向上滑动到顶部的时候
        index = 0
      }else if(index > this.listHeigth.length - 2  ){//向下滑动值下部最底端的时候
        index = this.listHeigth.length - 2
      }
       this.$refs.listview.scrollToElement(this.$refs.listGroup[index],0)/*调用scroll组件下的scrollToElement
        方法滚动至 对应的位置
      */
       this.scrollY = -this.listHeigth[index]
    },
    scroll(pos){/*该方法与scroll中的scroll方法相关关联当 滑动时 scroll组件中的scroll就会执行传出包含滚动的
      x y 的值 this.scroll又被监听 监听中操作判断移到第几个列表
      */
      this.scrollY = pos.y
    },
    _calculateHeight(){/*获取每个大的列表的高度*/
      this.listHeigth = []
      const list = this.$refs.listGroup
      let height = 0
      this.listHeigth.push(height)
      for(var i = 0;i<list.length;i++){
        let item = list[i];
        height += item.clientHeight
        this.listHeigth.push(height)
      }
    }
  },
  watch: { 
    data() {/*监听当data 我们获取到的数据发生变化的时候 重新计算高度的数组*/
      setTimeout(()=>{
        this._calculateHeight() 
      },20)
    },
    scrollY(newY){/*对当前的y轴的监听*/
        //当滚动到顶部，newy》0
        const listHeight = this.listHeigth
        if(newY>0){/**/
          this.currentIndex = 0
          return
        }
        //在中间部分滚动
        for(let i=0;i<listHeight.length;i++){
          let height1 = listHeight[i]
          let height2 = listHeight[i+1]
          if( (-newY)>=height1 && -newY<height2){
            this.def = height2 + newY //def
            this.currentIndex = i
            return
          }
        }
        //当滚动到底部，且-newy大于最后一个元素的上限
        this.currentIndex = listHeight.length - 2
    },
    def(newVall){//实现fixed 部分的滑动过度效果
      let fisedTop = (newVall > 0 && newVall < titleHeight) ? newVall-titleHeight : 0
      if(this.fisedTop === fisedTop){
        return
      }
      this.fisedTop = fisedTop
      console.log(fisedTop)
      this.$refs.fixed.style.transform = 'translate3d(0,'+fisedTop+'px,0)'
    }
  } 
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .listview
    position: absolute
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .list-group
      padding-bottom: 30px
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
    .list-shortcut
      position: fixed
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>




