 <template>
 	<div class="singer" ref="singer"> 
		 <listview 
		 :data="singers" 
		 @select="selectSinger"
		 ref="list"
		 ></listview>
		  <router-view></router-view>
 	</div>
 </template>
 
<script type="text/ecmascript-6">
	import {getSingerList} from 'api/singer'/*singer 调入的是一个promise函数其中的文件获取路径都已经配置好了*/
	import {ERR_OK} from 'api/config'
	import Singer from 'common/js/singer'
	import Listview from 'base/listview/listview'//封装的一个类似电话簿的组件
	import {mapMutations} from 'vuex'
	import {playlistMixin} from 'common/js/mixin'

	let HOT_NAME = '热门'
	let HotNub= 10
	export default {
		mixins:[playlistMixin],
		data(){
			return {
		singers:[]
			}
		},
		created(){
			this._getSingerList()
		},
		methods: {
			handlePlaylist(playlist){
      const bottom = playlist.length > 0 ? '60px':''
      this.$refs.singer.style.bottom = bottom 
			this.$refs.list.refresh()
	},
			selectSinger(singer){//获取到子组件传递过来的信息
				this.$router.push({//在当前页面进行路由跳转
					path: '/singer/'+singer.id+''
				})
				this.setSinger(singer)
			},
			_getSingerList(){/*接口请求数据并获取*/
				getSingerList().then((res)=>{
					if(res.code == ERR_OK){
						this.singers = this.normalListFn(res.data.list)
					} 
				})
			},
			normalListFn(list){/*对当前的=获取到的数据进行重新的组建*/
				let map={
					hot:{
						title:HOT_NAME,
						items:[]
					}
				}
				list.forEach((eltm,index)=>{
					if(index<HotNub){/*
							床件热门歌手数据根据当前的HotNub数从获取到的元素中截取
						*/
						map.hot.items.push(new Singer({/*Singer 为定义的一个组件 可以返回一个对象 包含当前传入的信息 和 
								由该信息生成的图片路径
							*/
							id: eltm.Fsinger_mid,
							name: eltm.Fsinger_name,
						}))
					}
					let key = eltm.Findex
					if(!map[key]){//判断当歌单的列表中不存在当前关键字的对象时创建一个
						map[key]={
							title:key,
							items:[]
						}
					}
					map[key].items.push(new Singer({//向 当前的对象中添加对应的id和name
							id: eltm.Fsinger_mid,
							name: eltm.Fsinger_name,
					}))
				})
				/*处理map得到有序列表*/
				let hot = []
				let ret = []
				for (let key in map){//循环将map进行拆分将hot  和 key为A-Z的 进行分离
					let val = map[key]
					if(val.title.match(/[a-zA-Z]/)){
						ret.push(val)
					}else if(val.title == HOT_NAME){
						hot.push(val)
					}
				}
				ret.sort((a,b)=>{//将分离出来的ret(关键字为A-Z的部分进行排序)
					return a.title.charCodeAt(0) - b.title.charCodeAt(0)
				})
				return hot.concat(ret)
			},
			...mapMutations({//vuex 语法糖 获取/传递数据 
				setSinger: 'SET_SINGER'
			})
		},
		components:{
			Listview
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	.singer
		position: fixed
		top: 88px
		bottom: 0
		width: 100%
</style>


